@import "./variables.scss";

html {
  -webkit-text-size-adjust: none;
  touch-action: pan-y;
  /*prevent user scaling*/
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.full-screen-overlay {
  position: fixed;
  /* Sit on top of the page content */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  /* Black background with opacity */
  z-index: 99;
  /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer;
  /* Add a pointer on hover */
  min-height: 100vh !important;
  min-width: 100vw !important;
  position: fixed;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.margin-top-20 {
  margin-top: 20px;
}

.content-responsive {
  min-height: 100vh !important;
  padding-top: 50px !important;

  // Above Desktop
  @media (min-width: 992px) {
    padding-left: 300px !important;
  }
}

.bg-primary {
  background-color: $primary-color !important;
}

.text-white {
  color: white !important;
}

.margin-menu {
  margin-top: 40px;
  margin-bottom: 20px;

  @media (max-width: 992px) {
    margin-top: 10px;
    margin-bottom: 5px;
  }
}

.clickable {
  cursor: pointer;
}

.sidebar-menu {
  text-align: center;

  .menu-item {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 15px;

    &:hover,
    &.active {
      background: linear-gradient(90deg, $fifth-color, $sixth-color);
      background-size: 400% 400%;

      -webkit-animation: CSSGradientAnimator 30s ease infinite;
      -moz-animation: CSSGradientAnimator 30s ease infinite;
      animation: CSSGradientAnimator 30s ease infinite;
      text-decoration: underline;
    }
  }
}

@-webkit-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@-moz-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

.modal-adaptive-size {
  min-width: 80%;

  // Below Desktop
  @media (max-width: 992px) {
    min-height: 100%;
    min-width: 100%;
    margin: 0;
    top: 0;
  }
}

.modal-adaptive-size {

  // Below Desktop
  @media (max-width: 992px) {
    min-height: calc(100% - 110px);
  }
}

.margin-top-min {
  margin-top: 20px;
}

.margin-bottom-md {
  margin-bottom: 15px;

  @media (min-width: 992px) {
    margin-bottom: 30px;
  }
}

.text-align-adaptive-size {
  text-align: left;

  @media (max-width: 992px) {
    text-align: center;
  }
}

.bg-tuknuk-menu {
  background: $primary-color !important;
  background: linear-gradient(90deg, $primary-color 5%, $secondary-color 75%) !important;
}

.bg-tuknuk-content {
  background: $third-color;
  background: linear-gradient(180deg, $third-color 5%, $fourth-color 75%);
}

.round-50-cover {
  border-radius: 50%;
  object-fit: cover;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left !important;
}

.text-right {
  text-align: right;
}

.divisor {
  width: 75%;

  &.primary-color {
    border: none;
    border-top: 1px solid rgba(255, 255, 255, 0.299);
  }
}

.title-adaptive {
  font-size: 1.5em;

  @media (max-width: 992px) {
    font-size: 1.2em;
  }
}

.margin-bottom-sm {
  margin-bottom: 20px;
}

@media (max-width: 992px) {
  .hidden-tablet-and-mobile {
    display: none;
  }
}

@media (min-width: 992px) {
  .hidden-desktop {
    display: none;
  }
}

.input-tuknuk {
  background-color: $seventh-color !important;
  color: white;
  border-radius: 20px;

  .ant-input {
    background-color: $seventh-color !important;
    color: white;
    border-radius: 20px;
    // padding: 5px;
    // margin-left: 10px;
    // margin-right: 10px;
  }
}

.ant-input-suffix {
  // margin-right: 10px;
}

.ant-select-selection-placeholder {
  color: $placeholder-color !important;
}

.border-none {
  border: none !important;
}

input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $placeholder-color !important;
  opacity: 1;
  /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $placeholder-color !important;
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: $placeholder-color !important;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: $placeholder-color !important;
}

.card-tuknuk-label {
  margin: 0;
  font-size: 15px;
}

.card-tuknuk-value {
  margin: 0;
  font-size: 25px;
}

.card-tuknuk-span {
  margin: 0;
  font-size: 15px;
}

.card-tuknuk-icon {
  padding: 10px;
  border-radius: 50%;
  background: linear-gradient(90deg, $fifth-color, $sixth-color);
  background-size: 400% 400%;

  -webkit-animation: CSSGradientAnimator 30s ease infinite;
  -moz-animation: CSSGradientAnimator 30s ease infinite;
  animation: CSSGradientAnimator 30s ease infinite;
  text-decoration: underline;
}

.card-tuknuk {
  @media (min-width: 992px) {
    margin-right: 30px;
  }
}

.success-color {
  color: $success-color;
}

.text-bold {
  font-weight: bold;
}

.margin-left-small {
  margin-left: 3px;
}